














import Vue from "vue";
import { ProjectItemInfo } from "../ProjectItemInfo";
import { Icons } from "@/assets/images";
import ProjectRowCard from "./ProjectRowCard.vue";
import ProjectInfoWindow from "./ProjectInfoWindow.vue";

export default Vue.extend({
  name: "ProjectRow",
  props: {
    projectItemInfo: {
      type: ProjectItemInfo,
      required: true
    }
  },
  data() {
    return {
      Icons: Icons,
      IsItemSelected: false as boolean
    };
  },
  components: {
    ProjectRowCard,
    ProjectInfoWindow
  },
  methods: {
    onItemPressed: function(shouldBeSelected: boolean) {
      this.IsItemSelected = shouldBeSelected;
      this.$emit("itemPressed", this);
    },
    onClosed: function() {
      this.onItemPressed(false);
    }
  }
});
