











import Vue from "vue";
import { ProjectItemInfo } from "../ProjectItemInfo";
import { Icons } from "@/assets/images";
import ProjectCardFront from "./ProjectCardFront.vue";
import ProjectCardBack from "./ProjectCardBack.vue";

export default Vue.extend({
  name: "ProjectItem",
  props: {
    projectItemInfo: {
      type: ProjectItemInfo,
      required: true
    }
  },
  components: {
    ProjectCardFront,
    ProjectCardBack
  },
  methods: {
    onCardPress: function(clickEvent: any) {
      // If already selected, do nothing.
      if (this.IsCardSelected === true) {
        return;
      }
      this.$emit("itemPressed", this);

      this.IsCardSelected = true;
      const clickedCard = clickEvent.target;

      // Centers the card on the screen after it has
      // been flipped to the backside and rendered.
      this.$nextTick(function() {
        clickedCard.scrollIntoView({
          behavior: "auto",
          block: "center",
          inline: "start"
        });
        // If at the bottom of the screen, do nothing.
        if (
          window.innerHeight + Math.round(window.scrollY) >=
          document.body.offsetHeight
        ) {
          return;
        }
        // Otherwise, scroll a part of the screen height upwards.
        const scrollHeight = this.$el.scrollHeight;
        window.scrollBy(0, -scrollHeight / 5);
      });
    }
  },
  data() {
    return {
      Icons: Icons,
      IsCardSelected: false as boolean
    };
  }
});
