



















import Vue from "vue";
import { ProjectItemInfo } from "../ProjectItemInfo";
import { Icons } from "@/assets/images";

export default Vue.extend({
  name: "ProjectCardFront",
  props: {
    projectItemInfo: {
      type: ProjectItemInfo,
      required: true
    },
    showText: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  data() {
    return {
      Icons: Icons
    };
  },
  methods: {
    getImage() {
      return this.projectItemInfo.image ?? Icons.codeComputer;
    }
  }
});
