








































import Vue from "vue";
import ProjectCard from "@/components/projects/mobile/ProjectCard.vue";
import ProjectRow from "@/components/projects/desktop/ProjectRow.vue";
import ItemFilter from "@/components/generic/ItemFilter.vue";
import { getAllProjectItems } from "./ProjectItemReader";
import { ProjectItemInfo } from "./ProjectItemInfo";

export default Vue.extend({
  name: "ProjectContainer",
  components: {
    ProjectCard,
    ProjectRow,
    ItemFilter
  },
  data() {
    return {
      projectItems: getAllProjectItems() ?? [],
      // "any" is used as it did not work well to set it
      // as a ProjectItem Component type.
      currentCard: null as any,
      activeFilters: [] as string[]
    };
  },
  methods: {
    onItemPressed(card: Event) {
      // If there is already a card, deselect it first.
      if (this.currentCard !== null) {
        this.currentCard.IsCardSelected = false;
      }
      // Update selected card.
      this.currentCard = card;
    },
    onFilterChanged(event: any) {
      this.activeFilters = event.checkedNames;
    },
    isProjectActive(item: ProjectItemInfo) {
      // If there are no active tag filters, show item.
      if (this.activeFilters.length === 0) {
        return true;
      }

      // If filter is active and item is without tag, do not show.
      if (item.tags === null) {
        return false;
      }

      // If this item has a tag that is filtered, show the item.
      const intersectArray = this.activeFilters.filter(tag =>
        item.tags?.includes(tag)
      );

      if (intersectArray.length > 0) {
        return true;
      }

      return false;
    }
  },
  computed: {
    numberOfActiveItems() {
      const activeCVItems: ProjectItemInfo[] = this.projectItems.filter(
        item => this.isProjectActive(item) === true
      );

      return activeCVItems;
    }
  }
});
