























import Vue from "vue";
import { ProjectItemInfo } from "../ProjectItemInfo";
import { Icons } from "@/assets/images";
import ProjectRowContent from "./ProjectRowContent.vue";

export default Vue.extend({
  name: "ProjectRowCard",
  props: {
    projectItemInfo: {
      type: ProjectItemInfo,
      required: true
    }
  },
  data() {
    return {
      Icons: Icons
    };
  },
  methods: {
    onReadMorePressed: function() {
      console.log("Pressed For More Info");
      this.$emit("itemPressed", true);
    }
  },
  computed: {
    canExpand(): boolean {
      return (
        this.projectItemInfo.videoLink !== null ||
        this.projectItemInfo.mediaItems !== null
      );
    }
  },
  components: {
    ProjectRowContent
  }
});
